$background: green;

body {
    background-color: $background;
    font-family: 'Akaya Telivigala', sans-serif;
}

/* akaya-telivigala-regular - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Akaya Telivigala';
    font-style: normal;
    font-weight: 400;
    src: url('../assets/fonts/akaya-telivigala-v27-latin-regular.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
